import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import Api from "../../api/api";
import AppContext from '../../context/AppContext'
import jwt from 'jwt-decode'
import Loading from '../Loading/Loading'
import { store } from 'react-notifications-component';

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            formData: {},
            loading: false,
        };
    }

    componentDidMount() {
        if (this.context.user) {
            this.props.history.push('/')
        }
    }

    changeHandler = (e) => {
        let name = e.target.name;
        let value = e.target.value;
        let formData = this.state.formData;
        formData[name] = value;
        this.setState({ 'formData': formData });
    }

    submitHandler = async (e) => {
        let data = this.state.formData;
        e.preventDefault();
        //console.log(data);

        try {
            this.setState({ loading: true });
            let res = await Api.login(data.username, data.password)
            //console.log(res.data.accessToken)
            localStorage.setItem("accessToken", res.data.accessToken)
            let user = jwt(res.data.accessToken)
            //console.log(user)
            this.context.setUser(user)
            //Notification
            store.addNotification({
                title: "ng nhp thnh cng",
                message: `Xin cho ${user.accountName}`,
                type: "success",
                container: "top-center",
                dismiss: {
                    duration: 3000,
                    //showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            //redirect
            //console.log(this.props.location)
            let from = this.props.location.state && this.props.location.state.from
            this.setState({ loading: false })
            this.props.history.push(from || '/')
        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            if (err.response && err.response.status === 401) {
                store.addNotification({
                    title: "ng nhp tht bi",
                    message: "Ti khon hoc mt khu khng chnh xc",
                    type: "warning",
                    container: "top-center",
                    dismiss: {
                        duration: 5000,
                        showIcon: true,
                    },
                    animationIn: ["animate__slideInDown", "animate__animated"],
                    animationOut: ["animate__fadeOutUp", "animate__animated"],
                })
                return
            }
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })

        }
    }

    render() {
        return (
            <div className="container">
                <Loading show={this.state.loading} />
                <div className="row" style={{ marginTop: "40px" }}>
                    <div className="col-12 text-center">
                        <h1>H thng s lin lc in t</h1>
                        <h1>Trng Trung hc c s ABC</h1>
                    </div>
                </div>
                <div className="row" style={{ marginTop: "40px" }} >
                    <div className="col-6" style={{ margin: "auto" }}>
                        <form action="/login" onSubmit={e => this.submitHandler(e)}>
                            <div className="form-group">
                                <label >Tn ti khon:</label>
                                <input type="text" className="form-control" id="username"
                                    name="username"
                                    onChange={e => this.changeHandler(e)}
                                    required
                                />
                            </div>
                            <div className="form-group">
                                <label >Mt khu:</label>
                                <input type="password" className="form-control" id="pwd"
                                    name="password"
                                    onChange={e => this.changeHandler(e)}
                                    required
                                />
                            </div>
                            <button type="submit" className="btn btn-primary" style={{ marginRight: "10px" }}>ng nhp</button>
                            <Link to="/forgetpassword">Qun mt khu</Link>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

Login.contextType = AppContext

export default withRouter(Login);
