import {
    BsArrowLeftShort,
    BsArrowRepeat,
    BsTrash,
    BsSearch,
} from "react-icons/bs";
import { Modal } from "react-bootstrap";
import { Form } from "react-bootstrap";
import { GoPlus } from "react-icons/go";
import { FaPencilAlt } from "react-icons/fa";
import React from "react";
import "../../css/SchoolYear.css";
import { Button } from "react-bootstrap";
import { Table } from "react-bootstrap";
import Api from '../../api/api'
import Pagination from '../Pagination/Pagination'
import { store } from 'react-notifications-component';
import Loading from '../Loading/Loading'
import { withRouter } from 'react-router-dom'
import SelectSearch, { fuzzySearch } from 'react-select-search';
import 'react-select-search/style.css'
class Account extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            //state is by default an object
            accountList: [],
            studentList: [],
            teacherList: [],
            perpage: 10,
            loading: true,
            pagination: {
                currentPage: 1,
                lastPage: 1,
            },
            modalData: {
                accountId: 0,
                accountName: "",
                role: 0,
                userCode: ""
            },
            showModal: false,
            modalKind: "",
            modalLoading: true,
            modalEdited: false,
            searchValue: "",
        };
    }

    async componentDidMount() {
        this.setState({
            loading: true
        })
        try {
            let [teacherList, studentList] = await Promise.all([
                Api.getTeacherList(1, 1000000),
                Api.getStudentList(1, 1000000),
            ]);
            this.setState({
                teacherList: teacherList.data.result.data,
                studentList: studentList.data.result.data
            })
            await this.refresh(1, this.state.perpage)
        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            if (err.response && err.response.status === 400) {
                return
            }
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
        }
    }

    back = () => {
        this.props.history.goBack()
    }

    refresh = async (page, perpage, searchValue) => {
        this.setState({ loading: true })
        try {
            let res
            if (searchValue || this.state.searchValue) {
                res = await Api.searchAccountByName(page || this.state.pagination.currentPage, perpage || this.state.perpage, searchValue || this.state.searchValue)
            } else {
                res = await Api.getAccountList(page || this.state.pagination.currentPage, perpage || this.state.perpage)
            }
            let accountList = res.data.result.data
            let teacherList = this.state.teacherList
            let studentList = this.state.studentList
            let map = {}
            for (let i = 0; i < teacherList.length; i++) {
                map[teacherList[i].teacherCode] = {
                    ...teacherList[i]
                }
            }
            for (let i = 0; i < studentList.length; i++) {
                map[studentList[i].studentCode] = {
                    ...studentList[i]
                }
            }
            for (let i = 0; i < accountList.length; i++) {
                accountList[i] = {
                    ...accountList[i],
                    ...map[accountList[i].userCode]
                }
            }
            console.log(accountList)
            this.setState({ loading: false, accountList: accountList, pagination: res.data.result.pagination })

        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            if (err.response && err.response.status === 400) {
                store.addNotification({
                    title: "Thng bo",
                    message: "Danh sch rng",
                    type: "info",
                    container: "top-center",
                    dismiss: {
                        duration: 5000,
                        showIcon: true,
                    },
                    animationIn: ["animate__backInDown", "animate__animated"],
                    animationOut: ["animate__fadeOutUp", "animate__animated"],
                })
                return
            }
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
        }
    }

    getTeacherOption = () => {
        let list = this.state.teacherList
        let options = []
        for (let i = 0; i < list.length; i++) {
            let { teacherName, teacherCode } = list[i];
            options.push({
                name: teacherName + " - " + teacherCode,
                value: teacherCode
            })
        }
        return options
    }

    getStudentOption = () => {
        let list = this.state.studentList
        let options = []
        for (let i = 0; i < list.length; i++) {
            let { studentName, studentCode } = list[i];
            options.push({
                name: studentName + " - " + studentCode,
                value: studentCode
            })
        }
        return options
    }

    changePerPage = async (e) => {
        this.setState({ perpage: e.target.value })
        await this.refresh(this.state.pagination.currentPage, e.target.value)
    }
    changePage = async (page) => {
        await this.refresh(page, this.state.perpage)
    }

    renderTableHeader() {
        return (
            <tr>
                <th>STT</th>
                <th>Tn ti khon</th>
                <th>H tn</th>
                <th>M gio vin/hc sinh</th>
                <th>Loi ti khon</th>
                <th className="text-center"><BsTrash /></th>
                <th className="text-center"><FaPencilAlt /></th>
            </tr>
        )
    }

    renderTableData() {
        let sttBase = this.state.perpage * (this.state.pagination.currentPage - 1) + 1
        return this.state.accountList.map((data, index) => {
            const { accountName, studentName, role, teacherName, userCode } = data;
            return (
                <tr key={index}>
                    <td>{sttBase + index}</td>
                    <td><span className="text-primary" style={{ cursor: "pointer" }} onClick={() => this.showInfo(index)}>{accountName}</span></td>
                    <td>{role === 0 ? studentName : teacherName}</td>
                    <td>{userCode}</td>
                    <td>{role === 0 ? "Hc sinh" : role === 1 ? "Gio vin" : "Qun tr vin"}</td>
                    <td className="text-center"><BsTrash onClick={() => this.deleteAccount(index)} /></td>
                    <td className="text-center"><FaPencilAlt onClick={() => this.editAccount(index)} /></td>
                </tr>
            );
        });
    }

    addAccount = () => {
        this.setState({
            showModal: true,
            modalKind: "add",
            modalLoading: false,
            modalEdited: false,
            modalData: {
                accountId: 0,
                accountName: "",
                role: 0,
                userCode: ""
            }
        })
    }

    editAccount = async (index) => {
        let account = this.state.accountList[index]
        this.setState({
            showModal: true,
            modalKind: "edit",
            modalLoading: false,
            modalEdited: false,
            modalData: {
                ...account
            }
        })
    }

    deleteAccount = (index) => {
        let account = this.state.accountList[index]
        this.setState({
            showDelete: true,
            modalData: {
                ...account
            }
        })
    }

    showInfo = (index) => {
        let account = this.state.accountList[index]
        this.setState({
            showModal: true,
            modalKind: "info",
            modalLoading: false,
            modalEdited: false,
            modalData: {
                ...account
            }
        })
    }

    closeDelete = () => {
        this.setState({ showDelete: false });
    }

    closeModal = () => {
        this.setState({ showModal: false });
    }

    render() {
        if (this.state.loading) {
            return (
                <div className="container-fluid d-flex justify-content-center">
                    <div className="d-flex justify-content-center text-primary mt-auto mb-auto">
                        <div className="spinner-border" role="status">
                            <span className="sr-only">Loading...</span>
                        </div>
                    </div>
                </div >
            )
        }
        return (
            <div className="container">
                <Dialog
                    show={this.state.showModal}
                    kind={this.state.modalKind}
                    close={this.closeModal}
                    data={this.state.modalData}
                    loading={this.state.modalLoading}
                    setData={(data) => this.setState({ modalData: data, modalEdited: true })}
                    edited={this.state.modalEdited}
                    refresh={this.refresh}
                    getStudentOption={this.getStudentOption}
                    getTeacherOption={this.getTeacherOption}
                />
                <ConfirmDelete
                    show={this.state.showDelete}
                    close={this.closeDelete}
                    data={this.state.modalData}
                    refresh={this.refresh}
                />
                <div className="row">
                    <div className="col align-self-center d-flex">
                        <div className="align-self-center">
                            <BsArrowLeftShort size={50} onClick={this.back} />
                        </div>
                        <div className="h3 align-self-center mb-0">
                            Qun l ti khon
                        </div>
                    </div>
                </div>
                <br />
                <div className="d-flex">
                    <div>
                        <button type="button" className="btn btn-light" onClick={() => this.refresh()}>
                            <div className="d-flex">
                                <div>
                                    Ti li trang
                                </div>
                                <div className="ml-2">
                                    <BsArrowRepeat />
                                </div>
                            </div>
                        </button>
                    </div>

                    <div className="ml-3">
                        <button type="button" className="btn btn-light" onClick={this.addAccount}>
                            <div className="d-flex">
                                <div>
                                    Thm ti khon
                                </div>
                                <div className="ml-2">
                                    <GoPlus />
                                </div>
                            </div>
                        </button>
                    </div>
                    <div className="d-flex ml-4">
                        <div className="align-self-center" >
                            S lng bn ghi mi trang:
                        </div>
                        <div className="align-self-center" >
                            <select className="form-control ml-3" value={this.state.perpage} onChange={this.changePerPage} style={{ width: "80px" }}>
                                <option value="5">5</option>
                                <option value="10">10</option>
                                <option value="15">15</option>
                                <option value="20">20</option>
                                <option value="30">30</option>
                                <option value="50">50</option>
                                <option value="100">100</option>
                            </select>
                        </div>
                    </div>
                    <div className="d-flex ml-auto">
                        <div className="input-group">
                            <input type="text" className="form-control" placeholder="Tm kim" value={this.state.searchValue} onChange={e => this.setState({ searchValue: e.target.value })} />
                            <div className="input-group-append">
                                <button className="btn btn-primary" type="button" onClick={() => this.refresh(1)}><BsSearch /></button>
                            </div>
                        </div>
                    </div>

                </div>
                <br></br>
                <div style={{ minHeight: "430px" }}>
                    <Table striped bordered hover >
                        <thead className="table-header">
                            {this.renderTableHeader()}
                        </thead>
                        <tbody>{this.renderTableData()}</tbody>
                    </Table>
                </div>
                <Pagination pagination={this.state.pagination} changePage={this.changePage} />
            </div >
        );
    }
}

class ConfirmDelete extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
        }
    }

    delete = async () => {
        this.setState({ loading: true })
        try {
            await Api.deleteAccount(this.props.data.accountId)
            //console.log(res)
            this.setState({ loading: false })
            store.addNotification({
                title: "Thnh cng",
                message: `Xa ti khon thnh cng`,
                type: "success",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    //showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            this.props.close()
            this.props.refresh()
        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
        }

    }

    render() {
        return (
            <div>
                <Modal show={this.props.show} backdrop="static" keyboard={false} >
                    <Modal.Header>
                        <Modal.Title>Xc nhn xa</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="container">
                            {"Bn chc chn mun xa ti khon "} <b> {this.props.data.accountName} </b> {" ?"}
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.props.close}>Hy</Button>
                        <Button variant="danger" onClick={this.delete}>Xa</Button>
                    </Modal.Footer>
                </Modal>
                <Loading show={this.state.loading} />
            </div>

        )
    }
}

class Dialog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showConfirm: false,
            message: "Bn c chc chn mun thot khng ?",
            loading: false,
        }
    }

    getTitle = () => {
        if (this.props.kind === "add") {
            return "Thm ti khon"
        }
        if (this.props.kind === "edit") {
            return "Sa ti khon"
        }
        return "Thng tin ti khon"
    }

    getButton = () => {
        if (this.props.kind === "add") {
            return <Button onClick={this.addAccount}>Thm</Button>
        }
        if (this.props.kind === "edit") {
            return <Button onClick={this.editAccount}>Sa</Button>
        }
        return null
    }

    validateData = () => {
        const isAccountName = /^[a-zA-Z0-9]+$/;
        if (this.props.data.accountName.length > 30) {
            store.addNotification({
                title: "Nhp d liu khng chnh xc",
                message: `Tn ti khon khng c ln hn 30 k t`,
                type: "warning",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    //showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            return false;
        }
        if (!isAccountName.test(this.props.data.accountName)) {
            store.addNotification({
                title: "Nhp d liu khng chnh xc",
                message: `Tn ti khon ch cha k t ch hoc s, khng c b trng!`,
                type: "warning",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    //showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            return false;
        }
        let password = this.props.data.password ? this.props.data.password.slice() : "";
        let isDigit = password.replace(/[^0-9]/g, "").length > 0 ? 1 : 0;
        let isLetter = password.replace(/[^a-zA-Z]/g, '').length > 0 ? 1 : 0;
        let isPunctuation = (password.length - password.replace(/[.,\/#!$%\^&\*;:{}=\-_`~()]/g, "").length) > 0 ? 1 : 0;
        if (password.length < 8 || (isDigit + isLetter + isPunctuation < 2)) {
            store.addNotification({
                title: "Li",
                message: "Mt khu khng hp l! Mt khu phi di ti thiu 8 k t v cha 2 trong 3 loi ch, s, k t c bit!",
                type: "warning",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            return false;
        }
        if (!this.props.data.userCode) {
            store.addNotification({
                title: "Nhp d liu khng chnh xc",
                message: `Cha chn Hc sinh hoc Gio vin`,
                type: "warning",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    //showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            return false;
        }
        return true;
    }

    addAccount = async () => {
        if (!this.validateData()) {
            return;
        }
        this.setState({ loading: true })
        try {
            await Api.addAccount(this.props.data)
            //console.log(res)
            this.setState({ loading: false })
            store.addNotification({
                title: "Thnh cng",
                message: `Thm ti khon thnh cng`,
                type: "success",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    //showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            this.closeAll()
            this.props.refresh()
        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            if (err.response && err.response.data.message.code === "ER_DUP_ENTRY") {
                store.addNotification({
                    title: "Thm ti khon tht bi",
                    message: "Tn ti khon  s dng hoc gio vin/hc sinh ny  c ti khon",
                    type: "warning",
                    container: "top-center",
                    dismiss: {
                        duration: 5000,
                        showIcon: true,
                    },
                    animationIn: ["animate__backInDown", "animate__animated"],
                    animationOut: ["animate__fadeOutUp", "animate__animated"],
                })
                return
            }
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
        }
    }

    editAccount = async () => {
        if (!this.validateData()) {
            return;
        }
        this.setState({ loading: true })
        if (!this.props.data.password) {
            store.addNotification({
                title: "Cp nht ti khon tht bi",
                message: "Mt khu khng c  trng",
                type: "warning",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            this.setState({ loading: false })
            return
        }
        try {
            await Api.editAccount(this.props.data)
            //console.log(res)
            this.setState({ loading: false })
            store.addNotification({
                title: "Thnh cng",
                message: `Sa ti khon thnh cng`,
                type: "success",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    //showIcon: true,
                },
                animationIn: ["animate__slideInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
            this.closeAll()
            this.props.refresh()
        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            if (err.response && err.response.data.message.code === "ER_DUP_ENTRY") {
                store.addNotification({
                    title: "Cp nht ti khon tht bi",
                    message: "Tn ti khon  s dng hoc gio vin/hc sinh ny  c ti khon",
                    type: "warning",
                    container: "top-center",
                    dismiss: {
                        duration: 5000,
                        showIcon: true,
                    },
                    animationIn: ["animate__backInDown", "animate__animated"],
                    animationOut: ["animate__fadeOutUp", "animate__animated"],
                })
                return
            }
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
        }
    }

    close = () => {
        if (this.props.edited) {
            this.setState({
                showConfirm: true
            })
        } else {
            this.props.close()
        }
    }

    closeAll = () => {
        this.setState({ showConfirm: false })
        this.props.close()
    }

    changeHandler = (e) => {
        // console.log(e)
        let name = e.target.name
        let value = e.target.value
        let data = this.props.data
        data[name] = value
        this.props.setData(data)
    }

    closeConfirm = () => {
        this.setState({
            showConfirm: false
        })
    }

    render() {

        return (
            <div>
                <Modal size="lg" show={this.props.show} onHide={this.close} centered backdrop="static" keyboard={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>{this.getTitle()}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        {this.props.loading && <div className="container-fluid d-flex justify-content-center" style={{ height: "300px" }}>
                            <div className="d-flex justify-content-center text-primary mt-auto mb-auto">
                                <div className="spinner-border" role="status">
                                    <span className="sr-only">Loading...</span>
                                </div>
                            </div>
                        </div >}

                        {!this.props.loading &&
                            <div className="container-fluid">
                                <Form>
                                    <Form.Group>
                                        <Form.Label>Tn ti khon</Form.Label>
                                        <Form.Control
                                            type="text"
                                            placeholder="Nhp tn ti khon"
                                            name="accountName"
                                            value={this.props.data.accountName}
                                            onChange={this.changeHandler}
                                            readOnly={this.props.kind === "info"}
                                        />
                                    </Form.Group>
                                    <Form.Group>
                                        <Form.Label>Mt khu</Form.Label>
                                        <Form.Control
                                            type="password"
                                            placeholder="Nhp mt khu"
                                            name="password"
                                            value={this.props.data.password}
                                            onChange={this.changeHandler}
                                            readOnly={this.props.kind === "info"}
                                        />
                                    </Form.Group>
                                    <Form.Group>
                                        <Form.Label>Loi ti khon</Form.Label>
                                        <Form.Control
                                            as="select"
                                            name="role"
                                            value={this.props.data.role}
                                            onChange={(e) => { this.changeHandler({ target: { name: "userCode", value: "" } }); this.changeHandler(e) }}
                                            readOnly={this.props.kind === "info"}
                                        >
                                            <option value={0}>Hc sinh</option>
                                            <option value={1}>Gio vin</option>
                                            <option value={2}>Qun tr vin</option>
                                        </Form.Control>
                                    </Form.Group>
                                    <div className="form-group">
                                        <label >{this.props.data.role === 0 ? "Hc sinh" : "Gio vin"} </label>
                                        <div>
                                            <SelectSearch
                                                options={this.props.data.role === 0 ? this.props.getStudentOption() : this.props.getTeacherOption()}
                                                search
                                                filterOptions={fuzzySearch}
                                                emptyMessage="Khng tm thy"
                                                placeholder=" "
                                                onChange={v => { this.changeHandler({ target: { name: "userCode", value: v } }) }}
                                                value={this.props.data.userCode}
                                                readOnly={this.props.kind === "info"}
                                            />
                                        </div>
                                    </div>

                                </Form>
                            </div>
                        }
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.close}>Hy</Button>
                        {this.getButton()}
                    </Modal.Footer>
                </Modal>

                <Modal show={this.state.showConfirm} backdrop="static" keyboard={false} >
                    <Modal.Header>
                        <Modal.Title>Xc nhn</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="container">
                            {this.state.message}
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.closeConfirm}>Hy</Button>
                        <Button variant="danger" onClick={this.closeAll}>ng</Button>
                    </Modal.Footer>
                </Modal>
                <Loading show={this.state.loading} />
            </div>
        )
    }
}

export default withRouter(Account);
