import React, { Component } from "react";
import Api from "../../api/api";
import '../../css/Statistic/SubjectStatistic.css';
import { store } from 'react-notifications-component';
import SelectSearch, { fuzzySearch } from 'react-select-search';
import { BsArrowLeftShort } from 'react-icons/bs'
import { withRouter } from 'react-router-dom'
import Pdf from "react-to-pdf";
import ReactToPrint from 'react-to-print';
const ref = React.createRef()
class SubjectStatistic extends Component {
    constructor(props) {
        super(props);
        this.state = {
            schoolYearList: [],
            classList: [],
            iconSize: '15px',
            searchCondition: {},
            homeroomTeacher: "",
            date: new Date(),
            scoreList: [],
        };
    }

    async componentDidMount() {
        this.setState({
            loading: true
        })
        try {
            let [schoolYearList, classList] = await Promise.all([
                Api.getSchoolYearList(1, 1000000),
                Api.getClassList(1, 1000000)
            ]);
            let searchCondition = {
                schoolYearId: schoolYearList.data.result.data[schoolYearList.data.result.data.length - 1].schoolYearId,
                classId: classList.data.result.data[0].classId,
                term: 1,
            }
            //console.log(searchCondition)
            this.setState({
                classList: classList.data.result.data,
                schoolYearList: schoolYearList.data.result.data,
                searchCondition: searchCondition,
                loading: false
            })
        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            if (err.response && err.response.status === 400) {
                store.addNotification({
                    title: "Thng bo",
                    message: "Danh sch rng",
                    type: "info",
                    container: "top-center",
                    dismiss: {
                        duration: 5000,
                        showIcon: true,
                    },
                    animationIn: ["animate__backInDown", "animate__animated"],
                    animationOut: ["animate__fadeOutUp", "animate__animated"],
                })
                return
            }
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
        }
    }

    back = () => {
        this.props.history.goBack()
    }

    refresh = async (searchCondition) => {
        this.setState({ loading: true, showReport: false })
        try {
            let res = await Api.getSubjectReport(1, 1000000, searchCondition || this.state.searchCondition)
            console.log(res)
            this.setState({
                scoreList: res.data.data,
                homeroomTeacher: res.data.homeroomTeacher,
                loading: false,
                showReport: true,
            })
        } catch (err) {
            console.log(err)
            this.setState({ loading: false })
            if (err.response && err.response.status === 400) {
                store.addNotification({
                    title: "Thng bo",
                    message: "Danh sch rng",
                    type: "info",
                    container: "top-center",
                    dismiss: {
                        duration: 5000,
                        showIcon: true,
                    },
                    animationIn: ["animate__backInDown", "animate__animated"],
                    animationOut: ["animate__fadeOutUp", "animate__animated"],
                })
                return
            }
            store.addNotification({
                title: "H thng c li",
                message: "Vui lng lin h qun tr vin hoc th li sau",
                type: "danger",
                container: "top-center",
                dismiss: {
                    duration: 5000,
                    showIcon: true,
                },
                animationIn: ["animate__backInDown", "animate__animated"],
                animationOut: ["animate__fadeOutUp", "animate__animated"],
            })
        }
    }

    getSchoolYearOption = () => {
        let list = this.state.schoolYearList
        let options = []
        for (let i = 0; i < list.length; i++) {
            let { schoolYearId, schoolYear } = list[i];
            options.push({
                name: schoolYear,
                value: schoolYearId
            })
        }
        return options
    }

    getClassOption = () => {
        let list = this.state.classList
        let options = []
        for (let i = 0; i < list.length; i++) {
            let { classId, className } = list[i];
            options.push({
                name: className,
                value: classId
            })
        }
        return options
    }

    changeSearchCondition = (name, value) => {
        console.log(name, value)
        let searchCondition = this.state.searchCondition
        searchCondition[name] = value
        this.setState({ searchCondition: searchCondition })
    }

    renderTableData() {
        let row = []
        let list = this.state.scoreList
        for (let i = 0; i < list.length; i++) {
            const { nGioi, nKem, nKha, nNuGioi, nNuKem, nNuKha, nNuTB, nNuYeu, nTB, nYeu, subjectName, tlGioi, tlKem, tlKha, tlNuGioi, tlNuKem, tlNuKha, tlNuTB, tlNuYeu, tlTB, tlYeu } = list[i];
            row.push(
                <tr className="text-center" key={i}>
                    <td rowSpan="2" className="align-middle">{subjectName}</td>
                    <td>N</td>
                    <td>{nNuGioi}</td>
                    <td>{tlNuGioi}</td>
                    <td>{nNuKha}</td>
                    <td>{tlNuKha}</td>
                    <td>{nNuTB}</td>
                    <td>{tlNuTB}</td>
                    <td>{nNuYeu}</td>
                    <td>{tlNuYeu}</td>
                    <td>{nNuKem}</td>
                    <td>{tlNuKem}</td>
                </tr>
            )
            row.push(
                <tr className="text-center" key={"TC" + i}>
                    <td>TC</td>
                    <td>{nGioi}</td>
                    <td>{tlGioi}</td>
                    <td>{nKha}</td>
                    <td>{tlKha}</td>
                    <td>{nTB}</td>
                    <td>{tlTB}</td>
                    <td>{nYeu}</td>
                    <td>{tlYeu}</td>
                    <td>{nKem}</td>
                    <td>{tlKem}</td>
                </tr>
            )
        }
        return row
    }


    render() {
        if (this.state.loading) {
            return (
                <div className="container-fluid d-flex justify-content-center">
                    <div className="d-flex justify-content-center text-primary mt-auto mb-auto">
                        <div className="spinner-border" role="status">
                            <span className="sr-only">Loading...</span>
                        </div>
                    </div>
                </div >
            )
        }
        return (
            <div className="container">
                <div className="row">
                    <div className="col align-self-center d-flex">
                        <div className="align-self-center">
                            <BsArrowLeftShort size={50} onClick={this.back} />
                        </div>
                        <div className="h3 align-self-center mb-0">
                            Thng k kt qu mn hc
                        </div>
                    </div>
                </div>
                <div className="row mt-3">
                    <div className="col-9">
                        <form className="form-inline" >
                            <label>Nm hc:</label>
                            <div className="ml-1 select-school-year">
                                <SelectSearch
                                    options={this.getSchoolYearOption()}
                                    search
                                    filterOptions={fuzzySearch}
                                    emptyMessage="Khng tm thy"
                                    placeholder=" "
                                    value={this.state.searchCondition.schoolYearId}
                                    onChange={v => this.changeSearchCondition("schoolYearId", v)}
                                />
                            </div>
                            <label className="ml-2">Lp:</label>
                            <div className="ml-1 select-class">
                                <SelectSearch
                                    options={this.getClassOption()}
                                    search
                                    filterOptions={fuzzySearch}
                                    emptyMessage="Khng tm thy"
                                    placeholder=" "
                                    value={this.state.searchCondition.classId}
                                    onChange={v => this.changeSearchCondition("classId", v)}
                                />
                            </div>
                            <label className="ml-2">Hc k:</label>
                            <div className="ml-1 select-term">
                                <SelectSearch
                                    options={[{ name: "C nm", value: 0 }, { name: "1", value: 1 }, { name: "2", value: 2 }]}
                                    search
                                    filterOptions={fuzzySearch}
                                    emptyMessage="Khng tm thy"
                                    placeholder=" "
                                    value={this.state.searchCondition.term}
                                    onChange={v => this.changeSearchCondition("term", v)}
                                />
                            </div>
                            <button type="submit" className="btn btn-primary ml-3" onClick={(e) => { e.preventDefault(); this.refresh() }}>Xem kt qu</button>
                        </form>
                    </div>
                    {this.state.showReport &&
                        <div className="col-3">
                            <Pdf targetRef={ref} filename="subject-report.pdf" scale={0.7} y={7}>
                                {({ toPdf }) => <button type="button" className="btn btn-primary mr-2" onClick={toPdf}>Xut file</button>}
                            </Pdf>
                            <ReactToPrint
                                copyStyles={true}
                                trigger={() => {

                                    return <button type="button" className="btn btn-primary mr-2">In thng k</button>;
                                }}
                                content={() => this.componentRef}
                            />
                        </div>
                    }
                </div>
                <hr />
                {this.state.showReport &&
                    <div ref={ref} style={{ overflow: "auto" }} >
                        <div ref={el => (this.componentRef = el)} className="container-fluid text-center" id="report" style={{ width: "280mm" }}>
                            <div className="row">
                                <div className="col-12 text-center statistic-title">
                                    <div>
                                        <b>Thng k xp loi mn hc ca lp {this.state.classList[this.state.class]}</b><br />
                            Trng Trung hc c s ABC
                        </div>
                                </div>
                            </div>

                            <div className="row mt-3">
                                <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12 text-center">
                                    <span className="mr-5 ml-5">
                                        <b>Hc k: </b> {this.state.searchCondition.term === 0 ? "C nm" : this.state.searchCondition.term}
                                    </span>
                                    <span className="mr-5 ml-5">
                                        <b>Nm hc: </b> {this.state.schoolYearList.find(e => e.schoolYearId === this.state.searchCondition.schoolYearId) && this.state.schoolYearList.find(e => e.schoolYearId === this.state.searchCondition.schoolYearId).schoolYear}
                                    </span>
                                    <span className="mr-5 ml-5">
                                        <b>Gio vin ch nhim: </b> {this.state.homeroomTeacher}
                                    </span>
                                </div>
                            </div>

                            <div className="row mt-4">
                                <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                    <table className="table table-bordered table-striped">
                                        <thead className="text-center">
                                            <tr>
                                                <th rowSpan="2" colSpan="2"></th>
                                                <th colSpan="2">Gii</th>
                                                <th colSpan="2">Kh</th>
                                                <th colSpan="2">Trung Bnh</th>
                                                <th colSpan="2">Yu</th>
                                                <th colSpan="2">Km</th>
                                            </tr>
                                            <tr>
                                                <th>SL</th>
                                                <th>TL</th>
                                                <th>SL</th>
                                                <th>TL</th>
                                                <th>SL</th>
                                                <th>TL</th>
                                                <th>SL</th>
                                                <th>TL</th>
                                                <th>SL</th>
                                                <th>TL</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {this.renderTableData()}
                                        </tbody>
                                    </table>
                                </div>
                            </div>

                            <div className="row mt-3 mb-2 ">
                                <div className="col-xs-7 col-sm-7 col-md-7 col-lg-7"> </div>
                                <div className="col-xs-5 col-sm-5 col-md-5 col-lg-5 text-center ml-auto">
                                    H Ni, ngy {this.state.date.getDate()} thng {this.state.date.getMonth() + 1} nm {this.state.date.getFullYear()}<br />
                        Gio vin ch nhim<br />
                                    <br />
                                    <br />
                                    <br />
                                    {this.state.homeroomTeacher}
                                </div>
                            </div>
                        </div>
                    </div>
                }
            </div>
        );
    }
}

export default withRouter(SubjectStatistic);
